import React, {useEffect, useState} from 'react'
import {dbService} from '../../tools/fbase'
import './ResponsePage.css'

function ResponsePage() {
    const [responses, setResponses] = useState();
    const [load, setLoad] = useState(false);

    const getThisUserDatas = async () => {
        const rDatas = await dbService
            .collection('cakehouse')
            .get();
        
        let rData = rDatas.docs.map(doc => {
            return({...doc.data(), id:doc.id})
        });
        setResponses(rData);
        setLoad(true)
    }

    useEffect(() => {
        getThisUserDatas();
    }, [load])

    return (
        <div className="response__container" style={{color:'white'}}>
            {responses && <>
            <div style={{fontFamily:'SCDBold', fontSize:'2em'}}>
                 {responses.length}
             </div>
                <div style={{fontFamily:'SCDLight'}}>
                    {responses.map((item, index) => {
                        let day = new Date(item.created);
                        console.log()
                        return(
                            <div className="row">
                                <div>
                                     : {item.phonenumber}
                                </div>
                                <div style={{marginLeft:'10px'}}>  : 
                                    {day.getMonth()+1} {day.getDate()}
                                    {day.getHours()} {day.getMinutes()}
                                </div>
                            </div>
                        )
                    })}
                </div>
            </>}
        </div>
    )
}

export default ResponsePage
